import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import ContactConsent from "./ContactConsent";
import emailjs from "@emailjs/browser";
import { useNavigate } from "react-router-dom";
import { useLang } from "../Header/LangContext";
import { useRef } from "react";
import "./Contact.css";

const ContactProposal = () => {
  const lang = useLang();
  const form = useRef();
  const navigate = useNavigate();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_09p1a4j",
        "template_g3bos26",
        form.current,
        "FYYZqMNi8ET9baB-j"
      )
      .then(
        (result) => {
          navigate("/submitted");
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="scroll">
      <Header headerStyle={"header initialPos"} />
      <div className="contact">
        <div className="contact__FormHeader">
          <h2>{lang ? " " : "PROJECT/SCENARIO PROPOSAL"}</h2>
        </div>

        <div className="contact__FormInfo">
          <p className="contact__InfoHeader">
            {lang ? " :" : "Submission of documents:"}
          </p>
          <p>
            {lang
              ? "     () "
              : "Synopsis and script of a proposal or scenario (required) "}
          </p>
        </div>

        <form ref={form} onSubmit={sendEmail}>
          <div className="contact__formDiv">
            <div className="contact__FormField">
              <p>
                <label htmlFor="formType">
                  {lang ? "  ." : "Proposal or Scenario"}
                </label>
              </p>
              <select name="formType" className="form-select">
                <option defaultValue="select">
                  {lang ? " ." : "Please select an option"}
                </option>
                <option defaultValue="proposal">
                  {lang ? "" : "Proposal"}
                </option>
                <option defaultValue="scenario">
                  {lang ? "" : "Scenario"}
                </option>
              </select>
            </div>

            <div className="contact__FormField">
              <p>
                <label htmlFor="name">{lang ? "." : "Name"}</label>
              </p>
              <input type="text" name="name" className="form-control" />
            </div>

            <div className="contact__FormField">
              <p>
                <label htmlFor="birthday">
                  {lang ? "." : "Birthday"}
                </label>
              </p>

              <input type="date" name="birthday" className="form-control" />
            </div>

            <div className="contact__FormField form-check">
              <div className="contact__GenderField">
                <label htmlFor="gender">{lang ? "" : "Gender"}</label>
              </div>
              <div className="form-check form-check-inline">
                <label htmlFor="male">{lang ? "" : "male"}</label>
                <input
                  type="radio"
                  id="male"
                  name="gender"
                  defaultValue="Male"
                  className="form-check-input"
                />
              </div>
              <div className="form-check form-check-inline">
                <label htmlFor="female">{lang ? "" : "female"}</label>
                <input
                  type="radio"
                  id="female"
                  name="gender"
                  defaultValue="Female"
                  className="form-check-input"
                />
              </div>
              <div className="form-check form-check-inline">
                <label htmlFor="neutral">{lang ? "" : "neutral"}</label>
                <input
                  type="radio"
                  id="neutral"
                  name="gender"
                  defaultValue="Neutral"
                  className="form-check-input"
                />
              </div>
            </div>

            <div className="contact__FormField">
              <p>
                <label htmlFor="email"> {lang ? "" : "Email"}</label>
              </p>
              <input
                type="email"
                name="email"
                className="form-control"
                placeholder="name@example.com"
              />
            </div>

            <div className="contact__FormField mb-3">
              <label htmlFor="plan">{lang ? "" : "Plan"}</label>
              <textarea
                className="form-control"
                name="plan"
                id="plan"
                cols="30"
                rows="10"
              ></textarea>
            </div>

            <div className="contact__FormField mb-3">
              <label htmlFor="scenario" className="form-label">
                {lang ? "" : "Scenario"}
              </label>
              <textarea
                className="form-control"
                name="scenario"
                id="scenario"
                cols="30"
                rows="10"
              ></textarea>
            </div>

            <div className="contact__FormField mb-3">
              <label htmlFor="extra" className="form-label">
                {lang ? "" : "Additional"}
              </label>
              <textarea
                className="form-control"
                name="extra"
                id="extra"
                cols="30"
                rows="5"
              ></textarea>
            </div>

            <div className="mb-3 contact__FormField">
              <label htmlFor="summary" className="form-label">
                {lang ? "  PR." : "Summary and Extra Information"}
              </label>
              <textarea
                className="form-control"
                rows="3"
                name="summary"
              ></textarea>
            </div>
          </div>
          <div className="contact__Data">
            <div className="overflow-auto contact__DataPolicy">
              <ContactConsent />
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                name="consent"
                defaultValue=""
                id="flexCheckDefault"
              />

              <label className="form-check-label" htmlFor="flexCheckDefault">
                {lang
                  ? "    ."
                  : "I agree to the collection and use of personal information."}
              </label>
            </div>
          </div>
          <div>
            <div className="contact__FormSubmit">
              <input
                className="btn btn-secondary"
                type="submit"
                defaultValue={lang ? "" : "Submit"}
              />
            </div>
          </div>
        </form>
      </div>
      <Footer />
    </div>
  );
};

export default ContactProposal;
